"use client";
import { useSpaceData } from "@/src/context/SpaceDataContext";
import { Crew } from "@/src/types";
import React, { useEffect, useState } from "react";
import Image from "next/image";

export default function page() {
  const [crewMember, setCrewMember] = useState<Crew>();
  const { data } = useSpaceData();

  useEffect(() => {
    if (!data) return;
    setCrewMember(
      data?.crew.find((crew: Crew) => crew.name === "Douglas Hurley")
    );
  }, [data]);

  if (!data) return null;
  return (
    <div className=" h-full flex flex-col gap-3">
      <p className=" text-2xl mx-auto barlowCondensed text-silver font-bold lg:mx-0">
        <span className="text-zinc-600">02</span> MEET YOUR CREW
      </p>
      <section className="flex-1">
        <section className="w-full aspect-square relative border-b-1 border-white/30">
          {crewMember && (
            <Image
              src={crewMember?.images.webp}
              alt={crewMember?.name}
              fill
              className="object-contain"
            ></Image>
          )}
        </section>
      </section>
      <section className="flex-1 bg-blue-300">
        <div className="flex items-center gap-3">
          <div className="w-4 aspect-square bg-white cursor-pointer rounded-full"></div>
        </div>
      </section>
    </div>
  );
}
